
import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';

interface Payouts {
    Excessamountid: string;
    Loanaccountno: string;
    Borrowerid: string;
    Borroweraccountno: string;
    Borrowername: string;
    Borrowerbankname: string;
    Borrowerifsccde: string;
    Excessamount1: string;
    Excessamountstatus: string;
}

@Component
export default class AppComponent extends Vue {

    itemNewCount: number = 0;
    
    newRefundRequest: Payouts[] = [];
    

    mounted() {
        let ele = document.getElementById('dashboardmenu') as HTMLElement;
        ele.style.display = "block";
        let element = document.getElementById('defaultclass') as HTMLElement;
        element.classList.remove("defaultHeight");
        element.classList.add("main-wrapper");

        this.GetRefundRequests();


    }


    GetRefundRequests() {

        axios.get('http://localhost:63151/api/Trustee/GetExcessAmountDetails', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        })
            .then(response => {
                alert(response.data.ResponseBody);
                this.newRefundRequest = response.data.ResponseBody;
                this.itemNewCount = response.data.ResponseBody.length;
                console.log(response.data.ResponseBody);
            })
            .catch(error => {
                console.log(error);

            })

    }




}



