
import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';


interface DashboardData {
    EscrowlendinBalance: string;
    EscrowRepaymentBalance: number;
    LenderPayoutPending: number;
    ATLPayoutPanding: number;
    NewLoanCount: number;
    LenderWithdrawalCount: number;
    BorrowerRefundCount: number;
    SuspectAccountCount: number;
}

interface UserDetailsInfo {
    userToken: string;
    userName: string;
    userRole: string;
    userSessionId: string;
}


@Component
export default class AppComponent extends Vue {
    dashboards: DashboardData[] = [];
    currentUserInfo: UserDetailsInfo[] = [];
    token: string = "";
    
    mounted() {


        let ele = document.getElementById('dashboardmenu') as HTMLElement;
        ele.style.display = "block";
        let element = document.getElementById('defaultclass') as HTMLElement;
        element.classList.remove("main-wrapper");
        element.classList.add("defaultHeight");
        this.LoadCurrentUserInformation();
 

    }

    //used to get the Dashboard Information
    GetDashBoardData() {
        
     axios.get('http://localhost:63151/api/Trustee/GetDashboardData', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        })
         .then(response => {
             if (response.data.ResponseCode == 1005) {
                 this.dashboards = response.data.ResponseBody;
             }
             else
             {
                 alert("Some Error Occured!!");
             }
             
            })
            .catch(error => {               
                    this.logout();              
            })
       
    }


    //get current users on page load
    LoadCurrentUserInformation() {
        fetch('http://localhost:49319/Home/GetUsers')
            .then(response => response.json())
            .then(data => {
                localStorage.setItem('userToken', data.accessToken);
                localStorage.setItem('userName', data.name);
                localStorage.setItem('userRole', data.profile);
                localStorage.setItem('userSessionId', data.sessionId);
                if (data.accessToken != null) {
                    this.currentUserInfo = data;
                    if (localStorage.getItem('userRole') != 'Trustee_Maker') {
                        this.logout();
                    }
                    else
                    {
                        this.GetDashBoardData();
                    }
                   
                }
                else
                {
                    this.logout();
                }               


            }).catch(error => {
                this.logout();
            })
    }

    //logout
    logout() {
        localStorage.clear();
        window.location.href = 'http://localhost:49319/home/logout';
        this.currentUserInfo = [];
        console.log(this.currentUserInfo);
    }

}



