
import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';

interface SuspectAccount {
    Suspectamountid: string;
    Loanaccountno: string;
    Borrowername: string;
    Borrowervirtualaccno: string;
    Borroweraccountno: string;
    Borrowerifsccde: string;
    Borrowerbankname: string;
    Borrowermobileno: string;
    Transactionrefno: string;
    Creditdate: string;
    Suspenseamount: string;
    Suspectamountstatus: string;
}

@Component
export default class AppComponent extends Vue {

    itemNewCount: number = 0;
    newSuspectAccountRequest: SuspectAccount[] = [];


    mounted() {
        let ele = document.getElementById('dashboardmenu') as HTMLElement;
        ele.style.display = "block";
        let element = document.getElementById('defaultclass') as HTMLElement;
        element.classList.remove("defaultHeight");
        element.classList.add("main-wrapper");

        this.GetRefundRequests();


    }

    GetRefundRequests() {

        axios.get('http://localhost:63151/api/Trustee/GetSuspenseAmountDetails', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        })
            .then(response => {

                this.newSuspectAccountRequest = response.data.ResponseBody;
                this.itemNewCount = response.data.ResponseBody.length;
                console.log(response.data.ResponseBody);
            })
            .catch(error => {
                console.log(error);

            })

    }




}



