
import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';
import * as Tablesaw from '../../assets/js/tablesaw.js';
import { forEachChild } from 'typescript';

interface LenderWithdrawals {
    Lenderid: string;
    Lendername: string;
    Lendervirtualaccno: string;
    Lenderaccname: string;
    Lendarbankname: string;
    Lendarbankifsccode: string;
    Lendaraccountno: string;
    Amountwidthdrawn: string;
    Lenderwidthdrawalstatus: string;
}

interface LenderWidthdrawalStatusDetails {
    LenderId: string;
    LenderwidhdrawalStatus: string;
    }


@Component
export default class AppComponent extends Vue {

    itemNewCount: number = 0;
    itemInTransitCount: number = 0;
    newLenderWithdrawanRequest: LenderWithdrawals[] = [];
    LenderWithdrawanRequestInTransit: LenderWithdrawals[] = [];
    exportRequest: LenderWithdrawals[] = [];
    inTransitExportRequest: LenderWithdrawals[] = [];
    updateStatusOfRequest: LenderWidthdrawalStatusDetails[] = [];
    inTransitIDValues: string[] = [];
    

    mounted() {
        let ele = document.getElementById('dashboardmenu') as HTMLElement;
        ele.style.display = "block";
        let element = document.getElementById('defaultclass') as HTMLElement;
        element.classList.remove("defaultHeight");
        element.classList.add("main-wrapper");

        element.classList.add("main-wrapper");
        (Tablesaw as any).init();

        //run after table saw is loaded
        let t = setInterval(() => {

            if (document.readyState === 'complete') {

                //for new request buttons
                var mainDiv = <HTMLElement>document.getElementById('tdNewRequest');
                var NewButtons = mainDiv.querySelectorAll("button");
                NewButtons[0].id = "Disburse";
                NewButtons[0].addEventListener("click", (e: Event) => this.APIDisburse());
                NewButtons[1].id = "ExportCSV";
                NewButtons[1].addEventListener("click", (e: Event) => this.ExportCSV());

                //for intransit buttons
                var inTransitDiv = <HTMLElement>document.getElementById('tdInTransitRequest');
                var InTransitButtons = inTransitDiv.querySelectorAll("button");
                InTransitButtons[0].id = "InDisburse";
                InTransitButtons[0].addEventListener("click", (e: Event) => this.APIDisburseIn());
                InTransitButtons[1].id = "InExportCSV";
                InTransitButtons[1].addEventListener("click", (e: Event) => this.ExportCSVIn());

                clearInterval(t)
            }
        }, 500)

        //check if current session is available
        if (localStorage.getItem('userToken') != null || localStorage.getItem('userToken') != 'undefined') {
            this.GetLenderWithdrawalRequests();
        }
        else {
            this.logout();
        }
        


    }

    APIDisburse() { }
    APIDisburseIn() { }


    ExportCSV() { }
    ExportCSVIn() { }

    GetLenderWithdrawalRequests() {

        axios.get('http://localhost:63151/api/Trustee/GetLenderWithdrawalDetails', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        })
            .then(response => {

                this.newLenderWithdrawanRequest = response.data.ResponseBody;
                this.newLenderWithdrawanRequest = this.newLenderWithdrawanRequest.filter(el => el.Lenderwidthdrawalstatus == "Approved");
                this.LenderWithdrawanRequestInTransit = response.data.ResponseBody;
                this.LenderWithdrawanRequestInTransit = this.LenderWithdrawanRequestInTransit.filter(el => el.Lenderwidthdrawalstatus == "InTransit");
                console.log(this.newLenderWithdrawanRequest);
                console.log(this.LenderWithdrawanRequestInTransit);
                this.itemNewCount = this.newLenderWithdrawanRequest.length;
                this.itemInTransitCount = this.LenderWithdrawanRequestInTransit.length;
               // console.log(response.data.ResponseBody);
            })
            .catch(error => {
                console.log(error);

            })

    }

   
    //logout method
    logout() {
        localStorage.clear();
        window.location.href = 'http://localhost:49319/home/logout';

    }


}



