import Vue from 'vue';
import { Component } from 'vue-property-decorator';

interface WeatherForecast {
    dateFormatted: string;
    temperatureC: number;
    temperatureF: number;
    summary: string;
}

//@Component
export default class FetchDataComponent extends Vue {
    forecasts: WeatherForecast[] = [];

    mounted() {
        let ele = document.getElementById('dashboardmenu') as HTMLElement;
        ele.style.display = "none";
        let element = document.getElementById('defaultclass') as HTMLElement;
        element.classList.remove("main-wrapper");
        element.classList.add("defaultHeight");

      
    }
}



