import './css/site.css';
import 'bootstrap';
import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);

const routes = [
    { path: '/', component: require('./components/DashBoard/dashboard.vue.html') },
    {
        path: '/Dashboard', component: require('./components/DashBoard/dashboard.vue.html')
    },
    {
        path: '/Login', component: require('./components/Login/login.vue.html')
    }
    , {
        path: '/ChangePassword', component: require('./components/ChangePassword/changepassword.vue.html')
    },
    {
        path: '/ForgotPassword', component: require('./components/ForgotPassword/forgotpassword.vue.html')
    },
    {
        path: '/NewLoanEnquiry', component: require('./components/NewLoanEnquiry/new_loan_enquiry.vue.html')
    },
     {
         path: '/Payouts', component: require('./components/Payouts/Payouts.vue.html')
    },
    {
        path: '/LenderWithdrawal', component: require('./components/LenderWithdrawal/LenderWithdrawal.vue.html')
    },
    {
        path: '/BorrowerRefund', component: require('./components/BorrowerRefund/BorrowerRefund.vue.html')
    },
    {
        path: '/SuspenseAccount', component: require('./components/SuspenseAccount/SuspenseAccount.vue.html')
    },
    {
        path: '/UploadTransactions', component: require('./components/UploadTransactions/TransactionRequests.vue.html')
    },
    {
        //path: '/Mobot', component: require('./components/BorrowersList/BorrowersList.vue.html')
        path: '/Mobot/:id', name: 'Mobot',component: require('./components/Mobot/Mobot.vue.html')
    },
    {
        path: '/Borrowers', component: require('./components/BorrowersList/BorrowersList.vue.html')
    }

];

new Vue({
    el: '#app-root',
    router: new VueRouter({ mode: 'history', routes: routes }),
    render: h => h(require('./components/app/app.vue.html'))
});



