import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';
import * as TablesawPayout from '../../assets/js/tablesaw.js'
import { forEachChild } from 'typescript';

class UserProfile {
    UserID: string;
    Name: string;
    Mobile: string;
    Email: string;
    DOB: string;
    DateOfEnrollment: string;
    MemberType: string;
    AadharNo: string;
    ProfileRemark: string;
    FoogleImapct: string;
}
class Bio {
    Ears: string;
    Face: string;
    EyeBrows: string;
    Eyes: string;
    UserID: string;
    Faceshape: string;
    Lips: string;
    Score: string;
    Chin: string;
    Nose: string;
    Cheeks: string;
}
class Bank {
    AvgSalaryDate: string;
    AvgBankCharges: string;
    UserID: string;
    AvgEmi: string;
    AverageSalary: string;
    AvgEcommSpend: string;
    AvgNoOfDebits: string;
    AvgNoOfATMWithdrawals: string;
    AvgWeeklyClosingBalance: string;
    AvgMinBalance: string;
    Income: string;
    CashMode: string;
    Accountstring: string;
    CreditCard: string;
    Branch: string;
    Statement: boolean;
    SalaryCredit: boolean;
    FamilyLoan: boolean;
}
class Facebook {
    UserID: string;
    Connections: string;
    ActivityRate: string;
    AgeOfAccount: string;
    puQalificationMatch: boolean;
    MaritalStatus: boolean;
    FamilyConnection: boolean;
    PresentLocation: string;
    RMN: string;
    Likes: string;
    Intrests: string;
    Hobbies: string;
}
class LookUp {
    Name: string;
    Id: string;

}
class LookupInfo {
    chinTypes = new LookUp();
    lipTypes = new LookUp();
    cheakTypes = new LookUp();
    noseTypes = new LookUp();
    eyeTypes = new LookUp();
    earTypes = new LookUp();
    faceTypes = new LookUp();
    eyebrowTypes = new LookUp();
    ageAccountTypes = new LookUp();
    incomeTypes = new LookUp();
    salaryDateTypes = new LookUp();
    cashModeTypes = new LookUp();
    avgBalanceTypes = new LookUp();
    spendingTypes = new LookUp();

}

class MobotInfo {
    MobotLookupDetails = new LookupInfo();
    UserProfileInfo = new UserProfile();
    BioInfo = new Bio();
    BankInfo = new Bank();
    FacebookInfo = new Facebook();
}


@Component

export default class AppComponent extends Vue {

    ///url = '';
    id: string = "";
    mobotBusiness: MobotInfo = new MobotInfo();
    lookupBusiness: LookupInfo = new LookupInfo();

    created() {

        this.id = this.$route.params.id;

    }
    mounted() {

        this.GetMobotDetails();
    }

    //This method is used to get new loan requests and then call intransit loan request
    GetMobotDetails() {
        this.mobotBusiness = new MobotInfo();
        this.lookupBusiness = new LookupInfo();
        var userId = JSON.stringify(this.id);
        alert(userId);
        axios({
            url: 'http://localhost:10017/api/Mobot/GetMobotData',
            method: 'post',
            data: userId,
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        }).then(response => {
            if (response.data.ResponseCode == 1005) {
                this.mobotBusiness.UserProfileInfo = response.data.Obj.UserProfileInfo;
                this.mobotBusiness.BioInfo = response.data.Obj.BioInfo;
                this.mobotBusiness.BankInfo = response.data.Obj.BankInfo;
                this.mobotBusiness.FacebookInfo = response.data.Obj.FacebookInfo;
                ///this.mobotBusiness.MobotImages = response.data.Obj.MobotImages;

                this.mobotBusiness.MobotLookupDetails.chinTypes = response.data.Obj.MobotLookupDetails.ChinTypes;
                this.mobotBusiness.MobotLookupDetails.lipTypes = response.data.Obj.MobotLookupDetails.LipTypes;
                this.mobotBusiness.MobotLookupDetails.cheakTypes = response.data.Obj.MobotLookupDetails.CheakTypes;
                this.mobotBusiness.MobotLookupDetails.noseTypes = response.data.Obj.MobotLookupDetails.NoseTypes;
                this.mobotBusiness.MobotLookupDetails.eyeTypes = response.data.Obj.MobotLookupDetails.EyeTypes;
                this.mobotBusiness.MobotLookupDetails.earTypes = response.data.Obj.MobotLookupDetails.EarTypes;
                this.mobotBusiness.MobotLookupDetails.faceTypes = response.data.Obj.MobotLookupDetails.FaceTypes;
                this.mobotBusiness.MobotLookupDetails.eyebrowTypes = response.data.Obj.MobotLookupDetails.EyebrowTypes;
                ////  this.mobotBusiness.lookUpDetails.ageAccountTypes = response.data.Obj.MobotLookupDetails.Age1AccountTypes;
                this.mobotBusiness.MobotLookupDetails.incomeTypes = response.data.Obj.MobotLookupDetails.IncomeTypes;
                //// this.mobotBusiness.lookUpDetails.salaryDateTypes = response.data.Obj.MobotLookupDetails.SalaryDateTypes;
                this.mobotBusiness.MobotLookupDetails.cashModeTypes = response.data.Obj.MobotLookupDetails.CashModeTypes;
                this.mobotBusiness.MobotLookupDetails.avgBalanceTypes = response.data.Obj.MobotLookupDetails.AvgBalanceTypes;
                this.mobotBusiness.MobotLookupDetails.spendingTypes = response.data.Obj.MobotLookupDetails.SpendingTypes;
            }
        })
            .catch(error => {
                // this.logout();
            })
    }

    UpdateMobotAPI() {
        var datavalue = JSON.stringify(this.mobotBusiness);
        var mobotInfo = {
            UserProfileInfo: this.mobotBusiness.UserProfileInfo,
            BioInfo: this.mobotBusiness.BioInfo,
            BankInfo: this.mobotBusiness.BankInfo,
            FacebookInfo: this.mobotBusiness.FacebookInfo
        }
        axios({
            url: 'http://localhost:10017/api/Mobot/UpdateMobotDetails',
            method: 'post',
            data: JSON.stringify(mobotInfo),
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        }).then(response => {
            if (response.data.ResponseCode == 1005) {
                this.GetMobotDetails();
                alert('Mobot Information Updated Successfull.');
            }
        })
            .catch(error => {
                // this.logout();
            })
    }
    
}


