import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';

interface BorrowerInfo {
    Id: string;
    UserId: string;
    Name: string;
    Mobile: string;
    Email: string;
    LobotScore: number;
    RegistrationSource: string;
}

@Component
export default class AppComponent extends Vue {

    borrowersTotalCount: number = 0;
    borrowerList: BorrowerInfo[] = [];


    mounted() {
        this.GetBorrowers();

    }


    GetBorrowers() {

        axios.get('http://localhost:10017/api/Mobot/GetBorrowerList', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken'),
                'Access-Control-Allow-Origin' : '*'
            }
        })
            .then(response => {
                this.borrowerList = response.data.Obj;
                this.borrowersTotalCount = response.data.Obj.length;
                    
            })
            .catch(error => {
                console.log(error);

            })

    }
    
}



