import Vue from 'vue';
import { Component } from 'vue-property-decorator';
import axios from 'axios';
import * as Tablesaw from '../../assets/js/tablesaw.js';
import { forEachChild } from 'typescript';





interface LoanRequest {
    JournalId: string,
    LoanId: number,
    TrasactionType: string,
    Amount: number,
    JournalStatus: number,
    TransactionNumber: string,
    Remarks: string,
    SourceAccountNumber: string,
    SourceIfscCode: string,
    BeneficiaryAccountNumber: string,
    BenefifiaryIfscCode: string,
    TransactionCategory: string,
    TrusteeTransactionId: string,
    SourceUserId: string,
    BeneficiaryUserId: string

    //Loanrequestid: string;
    //Borroweraccname: string;
    //Borroweraccountno: string;
    //Borrowerbankname: string;
    //Borrowerid: string;
    //Borrowerifsccde: string;
    //Borrowermobileno: string;
    //Borrowervirtualaccno: string;
    //Isloansplit: string;
    //Lendername: string;
    //Lendervirtualaccno: string;
    //Loanaccountno: string;
    //Loanamount: number;
    //Loandisbursementstatus: string;
    //Loanreferenceid: string;
    //Loanrequestdate: string;
    //Loansubtype: number;
    //Loantype: number;
    

}

interface LoanRequestStatus
{

    JournalId: string,
    LoanId: number,
    JournalStatus: number,
    TransactionNumber: string,
    Remarks: string

    //LoanReferencetId: string;
    //LoanDisbursementStatus: string;
}





@Component
export default class AppComponent extends Vue {

    itemNewCount: number = 0;
    itemInTransitCount: number = 0;
    newLoanRequest: LoanRequest[] = [];
    inTransitRequest: LoanRequest[] = [];
    exportRequest: LoanRequest[] = [];
    inTransitExportRequest: LoanRequest[] = [];
    updateStatusOfRequest: LoanRequestStatus[] = [];
    inTransitIDValues: LoanRequest[] = [];


    mounted() {

        //style
     
        let ele = document.getElementById('dashboardmenu') as HTMLElement;
        ele.style.display = "block";
        let element = document.getElementById('defaultclass') as HTMLElement;
        element.classList.remove("defaultHeight");
        element.classList.add("main-wrapper");
         (Tablesaw as any).init();

        //run after table saw is loaded
        let t = setInterval(() => {
          
            if (document.readyState === 'complete') {

                //for new request buttons
                var mainDiv = <HTMLElement>document.getElementById('tdNewRequest');
                var NewButtons = mainDiv.querySelectorAll("button");
                NewButtons[0].id = "Disburse";
                NewButtons[0].addEventListener("click", (e: Event) => this.APIDisburse());
                NewButtons[1].id = "ExportCSV";
                NewButtons[1].addEventListener("click", (e: Event) => this.ExportCSV());

                //for intransit buttons
                var inTransitDiv = <HTMLElement>document.getElementById('tdInTransitRequest');
                var InTransitButtons = inTransitDiv.querySelectorAll("button");
                InTransitButtons[0].id = "InDisburse";
                InTransitButtons[0].addEventListener("click", (e: Event) => this.APIDisburseIn());
                InTransitButtons[1].id = "InExportCSV";
                InTransitButtons[1].addEventListener("click", (e: Event) => this.ExportCSVIn());    
               
                clearInterval(t)
            }
        }, 500)

        //check if current session is available
        if (localStorage.getItem('userToken') != null || localStorage.getItem('userToken') != 'undefined') {
            this.GetNewloanRequests();
        }
        else
        {
            this.logout();
        }
    }
   


    //This method is used check and uncheck all the checkboxes when check all is checked or unchecked
    //required chec all checkbox id
    Checkall(ele: string) {
        var mainDiv = <HTMLElement>document.getElementById('tdNewRequest')
        var inTransitDiv = <HTMLElement>document.getElementById('tdInTransitRequest');

        //for new requests
        var element = <HTMLInputElement>document.getElementById('checkboxall');
        //for intransit
        var inTransitElement = <HTMLInputElement>document.getElementById('checkboxallintransit');

        if (ele == element.id) {
            var isChecked = element.checked;
            var checkboxes = mainDiv.querySelectorAll("input");
            if (isChecked) {
                for (var i = 0; i < checkboxes.length; i++) {
                    if (checkboxes[i].type == 'checkbox') {
                        checkboxes[i].checked = true;
                     }
                }
            } else {
                for (var i = 0; i < checkboxes.length; i++) {
                    if (checkboxes[i].type == 'checkbox') {
                        checkboxes[i].checked = false;
                     }
                }
            }
        }

        //for in transit requests
        if (ele == inTransitElement.id) {
            var isIntransitChecked = inTransitElement.checked;
            var inTransitCheckboxes = inTransitDiv.querySelectorAll("input");
            if (isIntransitChecked) {
                for (var i = 0; i < inTransitCheckboxes.length; i++) {
                    if (inTransitCheckboxes[i].type == 'checkbox') {
                        inTransitCheckboxes[i].checked = true;
                     }
                }
            } else {
                for (var i = 0; i < inTransitCheckboxes.length; i++) {
                    if (inTransitCheckboxes[i].type == 'checkbox') {
                        inTransitCheckboxes[i].checked = false;
                     }
                }
            }
        }


    }



    //to see if count is eqoual to all checkbox, then check all checkbox
    CheckAllCount()
    { 
       //for new
        var count = 0;
        var mainDiv = <HTMLElement>document.getElementById('tdNewRequest');
        var element = <HTMLInputElement>document.getElementById('checkboxall');
        var checkboxes = mainDiv.querySelectorAll("input");
        for (var i = 0; i < checkboxes.length; i++) {
            if (checkboxes[i].type == 'checkbox') {
                if (checkboxes[i].checked && checkboxes[i].id != 'checkboxall' && checkboxes[i].id != 'checkboxallintransit') {
                    count++;
                }
                
            }
        }
        if (count == (checkboxes.length-1))
        {
            element.checked = true;
        }
        else
        {
            element.checked = false;
        }

        //for intransit
        var incount = 0;
        var inTransitDiv = <HTMLElement>document.getElementById('tdInTransitRequest');
        var inTransiEelement = <HTMLInputElement>document.getElementById('checkboxallintransit');
        var checkboxes = inTransitDiv.querySelectorAll("input");
        for (var i = 0; i < checkboxes.length; i++) {
            if (checkboxes[i].type == 'checkbox') {
                if (checkboxes[i].checked && checkboxes[i].id != 'checkboxallintransit' && checkboxes[i].id != 'checkboxall') {
                    incount++;
                }
            }
        }
        if (incount == (checkboxes.length - 1)) {
            inTransiEelement.checked = true;
        }
        else {
            inTransiEelement.checked = false;
        }

    }

    
   //not implemented yet
    APIDisburse() {
        alert("API Disburse");
        
    }
    APIDisburseIn()
    {
        alert("api disburse InTransit");
    }


    //This method is used to export the selected new items in csv
    //update the status to intransit
    ExportCSV() {

        this.inTransitIDValues = [];
        this.exportRequest = [];
        var mainDiv = <HTMLElement>document.getElementById('tdNewRequest')
        var count = 0;

        //for new Request
        var element = <HTMLInputElement>document.getElementById('checkboxall');
        if (element.checked)
        {
            count++;
            this.exportRequest = this.newLoanRequest;
            this.inTransitIDValues = this.exportRequest;
            this.UpdateLoanRequestStatus(this.inTransitIDValues);         
          
        }
        
        if (!element.checked) {
            var checkboxes = mainDiv.querySelectorAll("input");;
            for (var i = 0; i < checkboxes.length; i++) {
                if (checkboxes[i].type == 'checkbox' ) {
                    if (checkboxes[i].checked && checkboxes[i].id != 'checkboxall' && checkboxes[i].id != 'checkboxallintransit') {
                        var filteredArray = this.newLoanRequest.filter(el => el.JournalId == checkboxes[i].id);
                        //This stores the requests need to be exported
                        this.exportRequest.push(filteredArray[0]);
                        //This contains list of loan request ids to be exported
                        this.inTransitIDValues.push(filteredArray[0]);
                        count++;
                    }
                   
                }
            }
            if (count > 0) {                
                 this.UpdateLoanRequestStatus(this.inTransitIDValues);                 
            }
            else
            {
                alert("Please select few records to export");
            }
            
        }

    }


    //This method is used to export intransit data in csv
    ExportCSVIn() {
        this.inTransitExportRequest = [];
        var inTransitDiv = <HTMLElement>document.getElementById('tdInTransitRequest');
        var count = 0;
        //for inTransit Requests
        var inTransitElement = <HTMLInputElement>document.getElementById('checkboxallintransit');
        if (inTransitElement.checked) {
            this.inTransitExportRequest = this.inTransitRequest;
            this.CallExportToExcel(this.inTransitExportRequest);
            count++;
        }

        if (!inTransitElement.checked) {
           
            var checkboxes = inTransitDiv.querySelectorAll("input");
            for (var i = 0; i < checkboxes.length; i++) {
                if (checkboxes[i].type == 'checkbox') {
                    
                    if (checkboxes[i].checked && checkboxes[i].id != 'checkboxallintransit' && checkboxes[i].id != 'checkboxall') {
                        var filteredArray = this.inTransitRequest.filter(el => el.JournalId == checkboxes[i].id);
                        this.inTransitExportRequest.push(filteredArray[0]);
                        count++;                         
                    }                    
                    
                }
            }
            if (count > 0) {
                this.CallExportToExcel(this.inTransitExportRequest);

                var checkboxes = document.querySelectorAll("input");
                for (var i = 0; i < checkboxes.length; i++) {
                    if (checkboxes[i].type =='checkbox')
                    checkboxes[i].checked = false;

                }
            }
            else {
                alert("Please select few records to export");
            }
            
        }
    }


    //This is generic method to call export to excel
    CallExportToExcel(value: LoanRequest[])
    {
        var datavalue = JSON.stringify(value);       
        axios({
            url: 'http://localhost:49319/Home/createFile',
            method: 'post',            
            data: datavalue,
            responseType: 'arraybuffer',
            headers: {
                'Accept': 'application/json; charset=utf-8',
                'Content-Type': 'application/json; charset=utf-8'
            }
        }).then((response) => {
            const url = window.URL.createObjectURL(new Blob([response.data]));
            const link = document.createElement('a');
            link.href = url;
            link.setAttribute('download', 'Loan_Requests.xlsx');
            document.body.appendChild(link);
            link.click();
        });
       
    }


    //This method is used to export the inline data
    GetRowDatainJson(ele: string) {
        this.exportRequest = [];
        var filteredArray = this.newLoanRequest.filter(el => el.JournalId == ele);
        this.exportRequest.push(filteredArray[0]);
        //make status as intransit
        this.inTransitIDValues.push(filteredArray[0]);
        //update loan status and call export to excel
        this.UpdateLoanRequestStatus(this.inTransitIDValues);       
    }



    //This method is used to get new loan requests and then call intransit loan request
    GetNewloanRequests() {
        this.newLoanRequest = [];
        this.itemNewCount = 0;
        this.inTransitRequest = [];
        var element = <HTMLInputElement>document.getElementById('checkboxallintransit');
        element.checked = false;
        axios.get('http://devbackofficeapi.azurewebsites.net/api/Reconciliation/GetJournalRecordsForTrustee?JournalStatus=4', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        })
            .then(response => {
                if (response.data.ResponseCode == 1005) {
                    this.newLoanRequest = response.data.ResponseBody;
                    this.itemNewCount = response.data.ResponseBody.length;
                    this.GetInTransitloanRequests();
                  }                    
            })
            .catch(error => {
               // this.logout();
            })

    }



    //This method is used to get Intransit loan requests
    GetInTransitloanRequests() {
        this.inTransitRequest = [];
        this.itemInTransitCount = 0;
        var element = <HTMLInputElement>document.getElementById('checkboxall');
        element.checked = false
        axios.get('http://devbackofficeapi.azurewebsites.net/api/Reconciliation/GetJournalRecordsForTrustee?JournalStatus=5', {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        })
            .then(response => {
                if (response.data.ResponseCode == 1005) {
                    this.inTransitRequest = response.data.ResponseBody;
                    this.itemInTransitCount = response.data.ResponseBody.length;
                }
              })
            .catch(error => {
                console.log(error);

            })
    }



    //This method will generate Model for update the status of selected new loan request, then calls update api and export to excel.
    UpdateLoanRequestStatus(updateStatus: LoanRequest[])
    {       
        this.updateStatusOfRequest = [];
        for (var i = 0; i < updateStatus.length; i++)
        {    

            this.updateStatusOfRequest.push({ "JournalId": (updateStatus[i].JournalId).toString(), "LoanId": updateStatus[i].LoanId, "JournalStatus": 5, "TransactionNumber": updateStatus[i].TransactionNumber, "Remarks": updateStatus[i].Remarks });
        }
       this.CallUpdateStatusAPI(this.updateStatusOfRequest);        
    }



    //This method will update the status of selected loan requests and call export to excel.
    CallUpdateStatusAPI(value: LoanRequestStatus[])
    {
       
        var datavalue = JSON.stringify(value);        
        axios({
            url: 'http://devbackofficeapi.azurewebsites.net/api/Reconciliation/UpdateJournalRecords',
            method: 'post',
            data: datavalue,
            headers: {
                'Accept': 'application/json; charset=utf-8',
                'Content-Type': 'application/json',
                'Authorization': 'Bearer ' + localStorage.getItem('userToken')
            }
        }).then(response => {
            debugger;
            if (response.data.ResponseCode == 1005)
            {
                this.CallExportToExcel(this.exportRequest);
                var checkboxes = document.querySelectorAll("input");
                for (var i = 0; i < checkboxes.length; i++)
                {
                    checkboxes[i].checked = false;
                   
                }
                this.GetNewloanRequests();
                
            }
            })
            .then(response => {
                this.GetInTransitloanRequests();                
            })
            .catch(error => {
                console.log(error);
            })

    }



    //logout method
    logout() {
        localStorage.clear();
        window.location.href = 'http://localhost:49319/home/logout';
      
    }


  

    

}





