import Vue from 'vue';
import { Component } from 'vue-property-decorator';


interface UserDetailsInfo {
    userToken: string;
    userName: string;
    userRole: string;
    userSessionId: string;
}
@Component
export default class AppComponent extends Vue {

    currentUserInfo: UserDetailsInfo[] = [];
     
    created() {

        this.LoadCurrentUserInformation();
        
    }
    mounted() {

        this.LoadCurrentUserInformation();
       


    }

    LoadCurrentUserInformation()
    {
        fetch('http://localhost:49319/Home/GetUsers')
            .then(response => response.json())
            .then(data => {
                
                localStorage.setItem('userToken', data.accessToken);
                localStorage.setItem('userName', data.name);
                localStorage.setItem('userRole', data.profile);
                localStorage.setItem('userSessionId', data.sessionId);

                if (data.accessToken != null) {
                    this.currentUserInfo = data;
                    if (localStorage.getItem('userRole') != 'Trustee_Maker')
                    {
                        this.logout();
                    }
                }
                else
                {
                    this.logout();
                }            

            })
         .catch (error => {
             this.logout();

        })
    }

    logout() {
        localStorage.clear();
        window.location.href = 'http://localhost:49319/home/logout';
        this.currentUserInfo = [];
        console.log(this.currentUserInfo);
    }
    
}

    





